* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.logo {
  font-size: 30px;
  display: inline-flex;
}
.card {
  text-align: center;
}
.card img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin: 10px auto;
  text-align: center;
}
.load {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid rgba(0, 0, 0, 0.062);
  border-bottom-color: dodgerblue;
  text-align: center;
  margin: 25px auto;
  animation: loading 1s infinite linear;
}
@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
ul li {
  list-style: none;
}
a {
  text-decoration: none;
}
.navbar {
  display: flex;
  padding: 15px;
  flex-direction: row;
  justify-content: flex-end;
  width: 75%;

  display: inline-flex;

  margin: 0 auto;
}
.icon {
  display: flex;

  flex-direction: row;
  justify-content: flex-end;
}

.navbar li {
  padding: 10px;
}
.navbar li a {
  color: white;
}
.img {
  width: 100%;
  min-height: 600px;
  background: url(../public/images/404.jpg) no-repeat center center;
  background-size: cover;
}
.dev {
  text-align: center;
  font-size: 18px;
  font-family: sans-serif;

  color: rgb(110, 109, 109);
  border-radius: 30px;

  transform: rotateX(-35deg);
  cursor: pointer;
  transition: 1s;
}
.p {
  perspective: 1000px;
}
.dev:hover {
  transform: rotateX(0);
}
.p {
  font-family: sans-serif;
  font-weight: bolder;
}
